import React, {useState} from 'react';
import {Route} from 'react-router';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import '../style.css'
import { motion } from "framer-motion"

const Search = () => {
    const [input, setInput] = useState("");
    const [changeClass, setChangeClass] = useState("addText");
    const [zooken, setZooken] = useState("");

    const gotoURL = () => {
        console.log("hello");
        setChangeClass("Search-expand");
        setZooken("Zoeken");
    }   
    
    const getInput = (e) => {
        e.preventDefault();
        console.log(e.nativeEvent.data);
        const value = e.nativeEvent.data;
        setInput(input + value);
    }   

    const routeAway = () => {
        window.open(`https://go.springest.achmea.nl/search/${input}`);
    }

    return(
        <div>
            <div className="Search-Bar">
                <form onSubmit={routeAway}>
                    <input id="search-bar" type="search"  placeholder="Hoi Martijn, waar ben je naar op zoek?" onChange={getInput} input={input}></input>
                    <div className={changeClass}>
                        <FontAwesomeIcon id="searchIcon" icon={faSearch} onClick={routeAway}></FontAwesomeIcon>
                        <p>{zooken}</p>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default Search;